import React, { useState } from "react";
import { db } from "./firebase"; // Importa tu configuraci√≥n de Firebase
import { collection, addDoc } from "firebase/firestore";
import "./Entrenadores.css"; // Importa tu archivo CSS para los estilos

const entrenadoresData = [
  {
    nombre: "Juan",
    apellidos: "P√©rez",
    especializacion: "Entrenamiento Personalizado",
    descripcion: "Juan tiene m√°s de 5 a√±os de experiencia ayudando a personas a alcanzar sus metas de fitness. Se especializa en entrenamiento funcional y nutrici√≥n.",
    email: "juan.perez@gymshark.com",
  },
  {
    nombre: "Mar√≠a",
    apellidos: "G√≥mez",
    especializacion: "Yoga y Meditaci√≥n",
    descripcion: "Con m√°s de 8 a√±os en la pr√°ctica de yoga, Mar√≠a ofrece clases que combinan relajaci√≥n y fortalecimiento f√≠sico.",
    email: "maria.gomez@gymshark.com",
  },
  {
    nombre: "Carlos",
    apellidos: "L√≥pez",
    especializacion: "Entrenamiento de Fuerza",
    descripcion: "Carlos es un apasionado del levantamiento de pesas y est√° aqu√≠ para ayudarte a maximizar tu fuerza y rendimiento.",
    email: "carlos.lopez@gymshark.com",
  },
  {
    nombre: "Ana",
    apellidos: "Mart√≠nez",
    especializacion: "Entrenamiento Cardiovascular",
    descripcion: "Ana se enfoca en la salud cardiovascular y ofrece rutinas de cardio personalizadas que se adaptan a las necesidades de cada cliente.",
    email: "ana.martinez@gymshark.com",
  },
  {
    nombre: "Luis",
    apellidos: "Fern√°ndez",
    especializacion: "Rehabilitaci√≥n y Fisioterapia",
    descripcion: "Luis tiene experiencia en fisioterapia y se especializa en ayudar a las personas a recuperarse de lesiones a trav√©s de ejercicios espec√≠ficos.",
    email: "luis.fernandez@gymshark.com",
  },
];

const Entrenadores = () => {
  const [showForm, setShowForm] = useState(false);
  const [formData, setFormData] = useState({
    nombre: "",
    apellidos: "",
    telefono: "",
    correo: "",
    experiencia: "",
  });

  const [successMessage, setSuccessMessage] = useState(""); // Estado para el mensaje de √©xito

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      await addDoc(collection(db, "entrenadores"), formData);
      setSuccessMessage("¬°Gracias por tu curr√≠culum! Nos pondremos en contacto pronto."); // Mostrar mensaje
      
      // Ocultar el mensaje despu√©s de 3 segundos
      setTimeout(() => {
        setSuccessMessage("");
      }, 3000);

      setShowForm(false);
      setFormData({ nombre: "", apellidos: "", telefono: "", correo: "", experiencia: "" }); // Limpiar el formulario
    } catch (error) {
      console.error("Error al enviar informaci√≥n:", error);
      setSuccessMessage("Error al enviar tu curr√≠culum. Int√©ntalo de nuevo.");
      
      // Ocultar el mensaje de error despu√©s de 3 segundos
      setTimeout(() => {
        setSuccessMessage("");
      }, 3000);
    }
  };

  return (
    <div className="entrenadores-container">
      <h1>Entrenadores Personales</h1>
      <button onClick={() => setShowForm(!showForm)}>
        {showForm ? "Cancelar" : "Enviar curr√≠culum"}
      </button>

      {/* Mostrar el mensaje de √©xito si existe */}
      {successMessage && <div className="success-message">{successMessage}</div>}

      {showForm && (
        <form onSubmit={handleSubmit} className="curriculum-form">
          <input
            type="text"
            name="nombre"
            placeholder="Nombre"
            value={formData.nombre}
            onChange={handleChange}
            required
          />
          <input
            type="text"
            name="apellidos"
            placeholder="Apellidos"
            value={formData.apellidos}
            onChange={handleChange}
            required
          />
          <input
            type="tel"
            name="telefono"
            placeholder="Tel√©fono"
            value={formData.telefono}
            onChange={handleChange}
            required
          />
          <input
            type="email"
            name="correo"
            placeholder="Correo electr√≥nico"
            value={formData.correo}
            onChange={handleChange}
            required
          />
          <textarea
            name="experiencia"
            placeholder="Experiencia previa"
            value={formData.experiencia}
            onChange={handleChange}
            required
          />
          <button type="submit">Enviar</button>
        </form>
      )}

      <div className="entrenadores-list">
        {entrenadoresData.map((entrenador, index) => (
          <div className="entrenador-card" key={index}>
            <h2>{entrenador.nombre} {entrenador.apellidos}</h2>
            <p><strong>Especializaci√≥n:</strong> {entrenador.especializacion}</p>
            <p><strong>Descripci√≥n:</strong> {entrenador.descripcion}</p>
            <p><strong>Email:</strong> <a href={`mailto:${entrenador.email}`}>{entrenador.email}</a></p>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Entrenadores;
